*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background: linear-gradient(120deg, #f6d365, #fda085);
  color: white;
  font-family: 'Poppins', sans-serif;
  min-width: 100vh;
}
header{
  font-size: 1.2rem;
}
header , form{
  min-height: 15vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
form input, form button{
  padding: 0.3rem;
  font-size: 2rem;
  border: none;
  background: #fff;
}
form button{
  color: #d88771;
  background: #fff;
  cursor: pointer;
  transform: all 0.3s ease;
}
form button:hover{
  background: #d88771;
  color: #fff;
}
.todo-container{
  display: flex;
  justify-content: center;
  align-items: center;
}
.todo-list{
  min-width: 30%;
  list-style: none;
}
.todo{
  margin: 0.4rem;
  background-color: #fff;
  color: #000;
  font-size: 1.4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.3s ease;
}
.todo li{
  flex: 1;
}
.trash-btn, .complete-btn{
  background: #ff6f47;
  cursor: pointer;
  color: #fff;
  font-size: 1rem;
  border:none;
  padding: 1rem;
}
.complete-btn{
  background:rgb(73, 204, 73);
}
.todo-item{
  padding: 0rem 0.5rem;
}
.fa-trash, .fa-check{
  pointer-events: none ;
}
.completed{
  text-decoration: line-through;
  opacity: 0.4;
}
.fall{
  transform: translateY(8rem) rotateZ(20deg);
  opacity: 0;
}
select{
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
  border:none;
}
.select{
  margin: 1rem;
  position: relative;
  overflow: hidden;
}
select{
  color: #ff6f47;
  width: 10rem;
  cursor: pointer;
  padding: 1rem;
}
.select::after{
  content: '\25bc';
  position: absolute;
  background: #ff6f47;
  top: 0;
  right:0;
  padding: 1rem;
  pointer-events: none;
  transition: all 0.3 ease;
}
.select:hover::after{
  background: #fff;
  color: #ff6f47;
}